.portfolio {
	&__top {
		padding: 36px 0 26px;
		text-align: center;
		@include sm {
			padding: 25px 0; }
		p {
			font-size: 24px;
			@include sm {
				font-size: 15px; } } }
	&__title {
		color: $green;
		text-transform: uppercase;
		font-size: 43px;
		margin-bottom: 24px;
		@include lg {
			font-size: 28px; }
		@include sm {
			font-size: 18px;
			margin-bottom: 15px; }
		span {
			font-size: 38px;
			@include lg {
				font-size: 28px; }
			@include sm {
				font-size: 18px; } } }
	&__content {
		position: relative; }
	&__bg-text {
		@include vcenter;
		font-size: 250px;
		color: #eae4d5;
		z-index: 0;
		letter-spacing: 35px;
		@include lg {
			display: none; } }
	&__list {
		background: rgba($gray, 0.7);
		font-size: 0;
		padding-top: 28px;
		position: relative;
		@include sm {
			padding-bottom: 5px; } }
	.bg-texture {
		top: 108px; }
	.btn-orange {
		margin-top: 54px;
		margin-bottom: 46px;
		@include md {
			margin-top: 25px;
			margin-bottom: 25px; } } }
.portfolio-item {
	display: inline-block;
	vertical-align: top;
	width: 33.333%;
	padding: 0 10px;
	margin-bottom: 60px;
	text-align: center;
	@include md {
		margin-bottom: 40px; }
	@include sm {
		display: block;
		margin: 0 auto 40px;
		width: 100%; }
	&__img {
		max-width: 260px;
		height: 260px;
		margin: 0 auto 15px;
		@include md {
			height: 220px; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__title {
		font-size: 20px;
		font-weight: 700;
		margin-bottom: 10px;
		@include sm {
			font-size: 16px; } }
	&__text {
		font-size: 18px;
		@include md {
			font-size: 14px; } } }
