.head-foot {
	display: flex;
	justify-content: space-between;
	padding: 25px 0 26px;
	@include md {
		display: block;
		padding: 15px 0;
		text-align: center; }
	&__logo {
		display: block;
		flex-grow: 0;
		padding-top: 4px;
		@include sm {
			img {
				max-width: 200px; } } }
	&__phone {
		text-align: center;
		flex-grow: 1;
		padding-top: 15px;
		@include md {
			margin-bottom: 25px; }
		a {
			display: inline-block;
			vertical-align: top;
			line-height: 1;
			font-weight: 700;
			color: $black;
			font-size: 0; }
		span {
			font-size: 56px;
			@include r(1170) {
				font-size: 45px; }
			@include sm {
				font-size: 26px; } }
		i {
			position: relative;
			top: 6px; }
		svg {
			fill: $black;
			width: 49px;
			height: 49px;
			margin-right: 7px;
			@include r(1170) {
				width: 44px;
				height: 44px; }
			@include sm {
				width: 30px;
				height: 30px; } }
		p {
			padding-top: 10px;
			a {
				font-size: 24px;
				@include sm {
					font-size: 16px; } } } }
	&__form {
		flex-basis: 262px;
		flex-shrink: 0;
		@include md {
			max-width: 500px;
			margin: 0 auto; }
		input {
			display: block;
			width: 100%;
			height: 44px;
			background-color: $white;
			border: 1px solid #3c0;
			color: $black;
			font-size: 18px;
			font-weight: 300;
			line-height: 44px;
			padding: 0 10px;
			margin-bottom: 18px;
			@include trans;
			@include sm {

				font-size: 16px; }
			@include placeholder {
				color: $gray2;
				font-weight: 300; }
			&.is-error {
				border-color: red; } } }
	.btn-g {
		width: 100%; } }
.bg-texture {
	min-width: 1920px;
	height: 975px;
	width: 100%;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	top: 0;
	background-position: 50% 0;
	z-index: 1;
	background-repeat: no-repeat;
	@include md {
		display: none; } }
.check {
	position: relative;
	padding-left: 41px;
	@include lg {
		margin-bottom: 20px; }
	@include sm {
		padding-left: 26px; }
	&__icon {
		position: absolute;
		top: 10px;
		left: 0;
		width: 30px;
		height: 25px;
		background-color: $green5;
		@include lg {
			top: 0; }
		@include sm {
			width: 20px;
			height: 15px; }
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: -7px;
			@include s(check);
			z-index: 1;
			@include sm {
				transform: scale(0.5);
				top: -15px;
				left: -7px; } } }
	span {
		font-size: 36px;
		color: $black;
		@include lg {
			font-size: 20px; }
		@include sm {
			font-size: 12px; } }
	&--bold {
		.check__icon {
			top: 5px;
			@include sm {
				top: -2px; } }
		span {
			font-size: 30px;
			font-weight: 700; } } }
.full-page {
	width: 100%;
	min-height: 100vh;
	position: relative;
	background: rgba($green-l, 0.5);
	padding-top: 15vh;
	@include rh(900) {
		padding-top: 50px; }
	@include sm {
		padding-top: 20px; }
	&--thanks {
		@include sm {
			padding-top: 50px; } }
	&__content {
		position: relative;
		z-index: 1;
		color: $white;
		text-align: center;
		max-width: 600px;
		margin: 0 auto;
		padding: 10px;
		background-color: #35b729;
		border-radius: 18px;
		box-shadow: 0 4px 4px rgba(30, 30, 29, 0.75);
		background-image: linear-gradient(to top, #35b729 0%, rgba(94, 216, 46, 0.57) 99%, rgba(94, 216, 46, 0.57) 100%);
		&--form {
			padding: 50px 10px;
			@include md {
				padding: 25px 10px; } } }

	&__title {
		font-size: 35px;
		font-weight: 700;
		text-transform: uppercase;
		margin-bottom: 20px;
		@include md {
			font-size: 24px;
			margin-bottom: 10px; }
		@include sm {
			font-size: 20px; } }
	&__text {
		font-size: 23px;
		margin-bottom: 20px;
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 16px; }
		&--sm {
			font-size: 18px;
			@include md {
				font-size: 16px; }
			@include sm {
				font-size: 14px; } } } }
.cform {
	max-width: 480px;
	margin: 0 auto;
	input {
		display: block;
		width: 100%;
		margin-bottom: 30px;
		background-color: $white;
		border: 1px solid #3c0;
		height: 61px;
		padding: 0 10px;
		font-size: 23px;
		line-height: 51px;
		color: $black;
		font-family: 'PT Sans', sans-serif;
		@include trans;
		border: 2px solid $white;
		@include placeholder {
			color: #7a7a7a; }
		&.is-error {
				border-color: red; }
		@include md {
			height: 40px;
			line-height: 40px;
			margin-bottom: 15px; } }
	.btn-orange {
		margin-top: 32px;
		width: 100%;
		@include md {
			max-width: 100%;
			margin-top: 20px; } } }
