.hero {
	padding-top: 160px;
	position: relative;
	@include md {
		padding-top: 0; }
	// z-index: 1
	&__top {
		background-color: rgba($green3, 0.6);
		padding-top: 40px;
		padding-bottom: 65px;
		position: relative;
		@include md {
			padding: 20px 0; } }
	&__bg-text {
		position: absolute;
		font-size: 588px;
		color: $green4;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		top: 0;
		line-height: 0.75;
		font-weight: 700;
		z-index: 0;
		@include lg {
			display: none; } }
	&__spec-text {
		text-align: center;
		font-size: 48px;
		text-transform: uppercase;
		font-weight: 700;
		color: $orange;
		margin-bottom: 56px;
		@include md {
			font-size: 26px; }
		@include sm {
			font-size: 18px;
			margin-bottom: 25px; } }
	&__inner {
		text-align: center;
		font-size: 0;
		@include sm {
			max-width: 300px;
			margin-left: auto;
			margin-right: auto; } }
	&__left, &__right {
		display: inline-block;
		vertical-align: top;
		text-align: left;
		@include r(499) {
			width: 50%; } }
	&__left {
		padding-right: 15px;
		@include sm {
			padding-right: 5px; } }

	&__right {
		padding-left: 15px;
		@include sm {
			padding-left: 5px; }
		// +r(499)
		// 	width: 40%

		p {
			font-size: 150px;
			color: $green;
			line-height: 0.6;
			font-weight: 700;
			@include sm {
				font-size: 50px;
				padding-top: 23px; }
			span {
				font-size: 48px;
				color: $black;
				line-height: 1;
				position: relative;
				top: -2px;
				@include sm {
					font-size: 20px;
					display: block;
					margin-top: 5px;
					padding-left: 5px; } } } }
	&__bottom {
		background: $gray;
		padding: 40px 0 0;
		@include sm {
			padding: 20px 0 5px; } }
	&__prod-list {
		display: flex;
		justify-content: space-between;
		@include sm {
			display: block; } }
	.title-lg {
		max-width: 940px;
		margin: 0 auto 18px; } }
.prod-block {
	flex-basis: 360px;
	flex-grow: 0;
	flex-shrink: 1;
	text-align: center;
	margin-bottom: 35px;
	padding: 0 5px;
	@include sm {
		max-width: 290px;
		margin: 0 auto 40px; }
	&__img {
		height: 550px;
		margin-bottom: 20px;
		@include md {
			height: 330px; }
		@include sm {
			margin-bottom: 10px;
			height: 290px; }
		img {
			object-fit: cover;
			width: 100%;
			height: 100%; } }
	&__title {
		font-size: 24px;
		font-weight: 700;
		margin-bottom: 15px;
		@include md {
			font-size: 20px; } }
	&__text {
		font-weight: 700; }
	.btn-g {
		margin-bottom: 18px;
		@include sm {
			margin-bottom: 10px; } } }
