.contacts {
	background: rgba($green-l, 0.5);
	padding-top: 10px;
	padding-bottom: 14px;
	position: relative;
	text-align: center;
	&__title {
		font-size: 42px;
		margin-bottom: 7px;
		@include md {
			font-size: 25px; } }
	&__map-wrapper {
		border: 5px solid $white;
		box-shadow: 0 5px 5px 0px  rgba($black, 0.3);
		margin-bottom: 7px;
		position: relative; }
	&__logo {
			position: absolute;
			top: 47%;
			transform: translateY(-50%);
			z-index: 1;
			left: 270px;
			@include lg {
				display: none; } }
	&__map {
		height: 598px;
		@include md {
			height: 300px; }
		@include sm {
			height: 250px; } }
	p {
		font-size: 30px;
		letter-spacing: 2px;
		@include md {
			font-size: 20px; }
		@include sm {
			font-size: 18px; } }
	.bg-texture {
		top: -50px; } }
