.wsection {
	position: relative;
	padding-top: 26px;
	padding-bottom: 35px;
	&__checks {
		margin-bottom: 58px;
		text-align: center;
		font-size: 0;
		@include sm {
			margin-bottom: 35px;
			text-align: left; }
		.check {
			margin: 0 15px;
			display: inline-block;
			vertical-align: top;
			@include lg {
				margin-bottom: 20px; }
			@include sm {
				display: inline-block;
				vertical-align: top;
				width: 50%;
				padding-right: 5px;
				margin: 0 0 10px;
				text-align: left; } } }
	&__items {
		max-width: 1040px;
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		margin: 0 auto;
		@include sm {
			display: block; } }
	&__text {
		text-align: center;
		font-size: 30px;
		margin-bottom: 30px;
		@include md {
			font-size: 20px; }
		@include sm {
			font-size: 16px; } }
	.title-sm {
		max-width: 1005px;
		margin: 0 auto 30px; }
	.title-line {
		margin-bottom: 30px; }
	.title-md {
		margin-bottom: 35px; }
	.bg-texture {
		top: -60px; } }
.block-item {
	text-align: center;
	margin-bottom: 34px;
	&__img {
		background-color: $green-l;
		width: 152px;
		height: 152px;
		margin: 0 auto 25px;
		border-radius: 50%;
		position: relative;
		@include md {
			width: 120px;
			height: 120px; }
		img {
			max-width: 81px;
			height: auto;
			max-height: 92px;
			@include vcenter;
			@include md {
				max-width: 70px;
				max-height: 80px; } } }
	&__title {
		font-size: 30px;
		margin-bottom: 20px;
		@include md {
			font-size: 20px; }
		@include sm {
			font-size: 16px; } }
	&__text {
		font-size: 24px;
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 14px;
			br {
				display: none; } } }
	&--small-text {
		.block-item__text {
			font-size: 18px;
			@include sm {
				font-size: 14px; } } } }
